import styled from "styled-components";
import typography from "../../Styles/typography";


const SearchStyled = styled.div`
  position: fixed;
  top:0;
  color: white;
  display: flex;
  flex-wrap: wrap;
  font-size: 1.2rem;
  z-index: 2;
  width: 100%;
  align-items: center;
  justify-content: space-evenly;
  background: rgba(0,0,0,0.5);
  padding-bottom:10px;
  h2 {
    color:white;
    text-align: center;
    width: 80%;
  }
  form {
    position: relative;
    input{
      width: 15rem;
    }
  }
  .hints{
    position: absolute;
    right: 0;
    display: flex;
    flex-direction:column;
    max-height: 50vh;
    overflow: auto;
    width: 15rem;
  }
  input {
    font-size: 1.2rem;
  }
  .hint {
    font-size: 1.2rem;
    border: none; 
    padding: 2px;
    text-align: left;
    background: ${typography.mainColor};
    color:${typography.fontColor};
    strong {
      font-weight: 900;
    }
    &:hover{
      background: darkgray;
    }
    &:focus{
      background: darkgray;
      outline: none;
    }
  }
  
`;


export default SearchStyled;